async function getStudentPromptAnswers() {
  var studentAnswers;
  await axios.post("https://cambio-api.herokuapp.com/api/auth/login", {username: "torgeir", password: "lysen123"}, config).then(res => 
  {
    token = res.data.token
    //alert(token)
  }
)
  await axios.get("https://cambio-api.herokuapp.com/api/SPrompt/", {headers: {'Authorization': `token ${token}`}}).then(res => {
    studentAnswers = res.data
  }).catch(err => alert(err))
  return studentAnswers
}
async function updateClass(newData) {
  await axios.get(`https://cambio-api.herokuapp.com/api/School_Class/1/`, {
        headers: {
        'Authorization': `token ${token}`
        }
        }).catch(err => console.log(err))
  await axios.patch(`https://cambio-api.herokuapp.com/api/School_Class/1/`, (newData), {
        headers: {
        'Authorization': `token ${token}`
        }
        }).catch(err => console.log(err))
}

